<template>
  <div class="login">
    <mt-header fixed title="登录">

    </mt-header>
    <mt-popup
      v-model="msgStatus"
      position="top"
      :modal="false"
      class="pop-up"
    >{{msg}}</mt-popup>
    <mt-field label="手机号" ref="phone" @input="phonewatch" :state="states.phone" v-model="obj.phone" placeholder="请输入手机号" type="tel"></mt-field>
    <mt-field label="密码"  ref="password" @input="passwordwatch" :state="states.password" v-model="obj.password" placeholder="请输入密码" type="password"></mt-field>
    <mt-button size="large" type="primary" @click="login">Login</mt-button>
    <mt-button size="large" type="danger" style="margin-top: 20px;">Forgot Password?</mt-button>

  </div>
</template>
<style scoped>
  .pop-up{
    background: rgba(0,0,0,0.5);
    width: 100%;
    line-height: 2.5;
    color: red;
  }
</style>
<script>
  import fetch from '../utils/fetch'
  export default {
    name: 'Login',
    data () {
      return {
        msg:'',
        msgStatus:false,
        obj:{
          phone: '',
          password: '',
        },
        states:{
          phone: '',
          password: ''
        }
      }
    },
    methods:{
      phonewatch(phone){
        if(phone.trim() !== ''){
          this.states.phone ='success'
        }else {
          this.states.phone ='error'
        }
      },
      passwordwatch(password){
        if(password.trim() !== ''){
          this.states.password ='success'
        }else {
          this.states.password ='error'
        }
      },
      login(){
        this.validate().then(()=>{
//          fetch.post('',{}).then(res=>{
//
//          }).catch(error=>{
//
//          })
          this.$router.push('/index')
        }).catch(error=>{
          this.msg = error;
          this.msgStatus = true;
          setTimeout(()=>{
            this.msgStatus = false;
          },1000)
        })
      },
      validate(){
        return new Promise( (reslove,reject) => {
          for (let  key in this.states){
            if (  this.states[key] !== 'success') {
              reject(key+' must not be empty');
            }
          }
          reslove();
        })
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



