<template>
  <div class="comment">
    <comment :comment="quote" :list="list" v-if="comment.quoteId>0"></comment>
    <div class="comment-text" v-html="comment.content"></div>
    <span class="username">#{{comment.count}} {{comment.userName}} </span>
  </div>
</template>

<script>
  import comment from  './comment.vue'
  export default {
    name: 'comment',
    props: ['comment','list'],
    computed:{
      quote(){
        return this.list['c'+this.comment.quoteId];
      }
    }
  }
</script>
<style>
  .emotion{
    max-height: 60px;
    max-width: 60px;
  }
  .username{
    color: #888;
    padding: 6px;
  }
  .comment{
    border: 1px solid #ccc;
    font-size: 0.8em;
    text-align: left;
    padding: 2px 1px 0;
  }
  .comment-text{
    padding: 6px;
    color: #666;
    word-break: break-all;
  }
</style>



