<template>
  <div class="article">
    <mt-header fixed title="文章">
      <mt-button  slot="left" icon="back" @click="$router.back()">返回</mt-button>
    </mt-header>

    <p class="title">{{article.title}}</p>
    <div class="content" v-html="article.article.content">

    </div>
    <div class="comments">
      <div class="comment-row" v-for="id in commentList" :key="id">
        <comment :comment="comments['c'+id]" :list="comments">
        </comment>
      </div>
    </div>
  </div>
</template>

<script>
  import fetch from '../utils/fetch'
  import comment from  './comment.vue'
  const EmojiUrl = 'http://cdn.aixifan.com/acfun-H5/public'
  export default {
    components:{
      comment:comment
    },
    data () {
      return {
        article:{article:{}},
        comments:[],
        commentList:[]
      }
    },
    created(){
      this.getArticle()
      this.getComments()
    },
    methods:{
      getArticle(){
        console.log(this.$route.params.id)
        fetch.get(`http://localhost:3000/acfun/articles/${this.$route.params.id}`).then(res=>{
          this.article = res.data.data;
          this.$nextTick(()=>{
            var eles = document.querySelectorAll('.content img');
            var maxwidth = window.innerWidth;
            eles.forEach(e=>{
              if(maxwidth<e.width+10|| e.width == 0){
                e.style.width = '90%'
              }
            })
          })
        }).catch(err=>{
          console.log(err)
        })
      },
      getComments(){
        fetch.get(`http://localhost:3000/acfun/articles/${this.$route.params.id}/comments`).then(res=>{
          if(typeof res.data =='string'){
            let data = JSON.parse(res.data.replace('system.tv=',""))
            this.comments = data.data.commentContentArr;
            for(let v in this.comments){
              let ele  = this.comments[v]
              ele.content = ele.content.replace(/\[emot\=(\S+?)\,(\S+?)\/\]/g, '<img class="emotion" src="' + EmojiUrl + '/images/expression/$1/$2.gif">').replace(/\[img\](\S+?)\[\/img\]/g, '<a class="thumb" href="$1" target="_blank" title="点击以浏览图像"><img class="preview" src="$1"></a>')
                .replace(/\[img\=(\S+?)\](\S+?)\[\/img\]/g, '<a class="thumb" href="$2" target="_blank" title="$1"><img class="preview" src="$2"></a>')
            }
            this.commentList = data.data.commentList;
          }else {
            this.comments = res.data.data.commentContentArr;
          }
        }).catch(err=>{
          console.log(err)
        })
      },
      formatComment(val){
//        handle emoji
        let reg =new RegExp('/^\[emot*\]$/')
        return val;
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .title{
    font-size: 1.1em;
    font-weight: bold;
  }
  .content{
    text-align: left;
  }
  .comment-row:not(:first-child){
    margin-top: 5px;
  }
</style>



